.navbar {
    background-color: #333;
    color: #fff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar__logo {
    display: flex;
    align-items: center;
  }
  
  .navbar__logo-image {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  
  .navbar__title {
    font-family: 'Nova Flat', sans-serif;
    font-size: 24px;
    font-weight: bold;
  }
  
  
  .navbar__sections a {
    color: #fff;
    text-decoration: none;
    margin-right: 10px;
    font-family: 'Nova Flat', sans-serif;
  }
  
  .navbar__sections a:hover {
    color: #0c57b9;
    transition: color 0.3s ease;
  }
  