.hero {
    text-align: center;
    padding: 20px;
  }
  
  .services {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .service {
    width: 400px;
    padding: 10px;
    background-color: #f0f0f0;
    border-radius: 5px;
    transition: transform 0.3s ease;
  }
  
  .service:hover {
    transform: scale(1.05);
  }
  
  .service h3 {
    margin-top: 0;
  }
  
  .service p {
    margin-bottom: 0;
  }
  